import "./share.css";
import {PermMedia,Label,EmojiEmotions,Room,Cancel} from "@material-ui/icons";
import { useContext,useRef,useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import axios from "axios";

export default function Share() {
  const {user}=useContext(AuthContext)
  const PF=process.env.REACT_APP_PUBLIC_FOLDER
  const desc=useRef()
  const [file,setFile]=useState(null);

  const submitHandler=async(e)=>{
    e.preventDefault();
    const newPost={
        userId:user._id,
        desc:desc.current.value,
    }
    if(file){
        const data=new FormData();
        const fileName=file.name
        data.append("file",file)
        data.append("name",fileName)
        newPost.img=fileName
        try{
            await axios.post("/api/upload",data)
            window.location.reload();

        }catch(err){
            console.log(err)

        }
    }

    try{
       await axios.post("/api/posts",newPost)

    }catch(err){

    }


  }
  return (
    <div className="share">
        <div className="shareWrapper">
            <div className="shareTop">
                <img className="shareProfileImg" src={user.profilePicture ? PF+user.profilePicture : PF+"noAvatar1.png"} alt="" />
                <input ref={desc} placeholder={"What's in your mind "+user.username+"?"} className="shareInput"/>
            </div>
            <hr className="shareHr"/>
            {file && (
                <div className="shareImgContainer">
                    <img className="shareImg" src={URL.createObjectURL(file)} alt="" />
                    <Cancel className="shareCancelImg" onClick={()=>setFile(null)}/>
                </div>

            )}
            <form className="shareBottom" onSubmit={submitHandler}>
                <div className="shareOptions">
                    <label htmlFor="file" className="shareOption">
                        <PermMedia htmlColor="tomato" className="shareIcon"></PermMedia>
                        <span className="shareOptionText">Photo or Video</span>
                        <input style={{display:"none"}} type="file" id="file" accept=".png,.jpeg,.jpg" onChange={(e)=>setFile(e.target.files[0])}/>
                    </label>
                    <div className="shareOption">
                        <Label htmlColor="blue" className="shareIcon"></Label>
                        <span className="shareOptionText">Tag</span>
                    </div>
                    <div className="shareOption">
                        <Room htmlColor="green" className="shareIcon"></Room>
                        <span className="shareOptionText">Location</span>
                    </div>
                    <div className="shareOption">
                        <EmojiEmotions htmlColor="goldenrod" className="shareIcon"></EmojiEmotions>
                        <span className="shareOptionText">Feelings</span>
                    </div>
 
                </div>
                <button className="shareButton" type="submit">Share</button>

            </form>

        </div>
    </div>
  )
}
