import "./messenger.css";
import Topbar from "../../components/topbar/Topbar";
import Conversation from "../../components/conversation/Conversation";
import Message from "../../components/message/Message";
import ChatOnline from "../../components/chatOnline/chatOnline";
import { useContext,useState,useEffect,useRef} from "react";
import { AuthContext } from "../../context/AuthContext";
import axios from "axios";

export default function Messenger(){
    const [conversations,setConversations]=useState([]);
    const {user}=useContext(AuthContext)
    const [currentChat,setCurrentChat]=useState(null);
    const [messages,setMessages]=useState([]);
    const [newMessage,setNewMessage]=useState("")
    const scrollRef=useRef();

    useEffect(()=>{
        const getConversations=async()=>{
            
            try{
                const res=await axios.get("/api/conversations/"+user._id)
                setConversations(res.data);

            }catch(err){
                console.log(err)

            }
            
        }
        getConversations();
    },[user._id])

    useEffect(()=>{
        const getMessages=async()=>{
            try{
                const res=await axios.get("/api/messages/"+currentChat?._id)
                setMessages(res.data)
                setNewMessage("")

            }catch(err){
                console.log(err)

            }
            
        }
        getMessages()

    },[currentChat])

    const handleSubmit=async (e)=>{
        e.preventDefault()
        const message={
            sebder:user._id,
            text:newMessage,
            conversationId:currentChat._id
        }
        try{
            const res=await axios.post("/api/messages",message)
            setMessages([...messages,res.data])

        }catch(err){
            console.log(err)

        }

    }
    useEffect(()=>{
        scrollRef.current?.scrollIntoView({behavior:"smooth"})
        

    },[messages])
    return (
        <>
            <Topbar/>
            <div className="messenger">
                <div className="chatMenu">
                    <div className="chatMenuWrapper">
                        <input className="chatMenuInput" placeholder="Search for friends " />
                        {[conversations]?.map((c)=>(
                            <div onClick={()=>setCurrentChat(c)}>
                                <Conversation conversation={c} currentUser={user}/> 
                            </div>
                            
                        ))}
                        
 
                        
                    </div>
                </div>
                <div className="chatBox">
                    <div className="chatBoxWrapper">
                        {
                            currentChat ? (
                        <>
                            <div className="chatBoxTop">
                                {messages?.map((m)=>(
                                    <div ref={scrollRef}>
                                      <Message message={m} own={m.sender===user._id}></Message>
                                    </div>
                                    
                                )
                                )}
                                


                            </div>
                            <div className="chatBoxBottom">
                            <textarea value={newMessage} onChange={(e)=>setNewMessage(e.target.value)} className="chatMessageInput" placeholder="write something.."></textarea>
                            <button onClick={handleSubmit} className="chatSubmitButton">Send</button>
                        </div>
                        </>):(<span className="noConversation">Open a Conversation to start a chat</span>)}
  


                    </div>
                </div>
                <div className="chatOnline">
                    <div className="chatOnlineWrapper">
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>
                        <ChatOnline/>

                    </div>
                </div>
            </div>
        </>
    );
}